/*****effect animation start********/

  
.effect-wrap .effect {
  position: absolute;
  z-index: 1;
}

/*****effect-1 triangle rotating animation start ********/

.effect-1 {
  width: 30px;
  height: 30px;
  border: 4px solid #8a49ff;
  right: 10%;
  bottom: 10%;
  -webkit-animation: spin 10s linear infinite;
          animation: spin 10s linear infinite;
}


@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
/*****effect-1 animation end ********/

/*****effect-2 animation start ********/

.effect-2 {
  left: 3%;
  bottom: 20%;
  display: flex;
  flex-flow: row wrap;
  width: 40px;
  justify-content: space-between;
  -webkit-animation: topBounce 4s ease-in-out infinite;
          animation: topBounce 4s ease-in-out infinite;
}

.effect-2 div {
  width: 3px;
  height: 3px;
  background: #ff9c07;
  margin: 0 3px 8px;
}
@-webkit-keyframes topBounce {
  0%,
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
  50% {
    -webkit-transform: translateY(20%);
            transform: translateY(20%);
  }
}
@keyframes topBounce {
  0%,
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
  50% {
    -webkit-transform: translateY(20%);
            transform: translateY(20%);
  }
}

/*****comment effect-2 animation end ********/

/*****effect-3 half circle animation start ********/

.effect-3 {
  height: 180px;
  width: 180px;
  border: 25px solid #6f799e;
  border-radius: 50%;
  left: 50%;
  top: -80px;
  -webkit-animation: leftBounce 10s ease-in-out infinite;
          animation: leftBounce 10s ease-in-out infinite;
}

@-webkit-keyframes leftBounce {
  0%,
  100% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
  }
  50% {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
  }
}

@keyframes leftBounce {
  0%,
  100% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
  }
  50% {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
  }
}

/*****effect-3 animation end ********/




/*****effect-4 triangle animation start ********/

.effect-4 {
  border-top: 30px solid transparent;
  border-left: 30px solid #06d79c;

  left: 30%;
  top: 30%;
  -webkit-animation: spin 15s linear infinite;
          animation: spin 15s linear infinite;
}
.effect-4::before {
  content: "";
  border-top: 30px solid transparent;
  border-left: 30px solid #06d79c;
  position: absolute;
  left: -35px;
  top: -25px;
  opacity: 0.5;

}
/*****effect-4 triangle animation end********/


/*****effect-5circular stripes animation start ********/

.effect-5{
    height: 50px;
    width: 50px;
    right: 15%;
    top: 20%;
    display: flex;
    justify-content: space-between;
    border-radius:50% ;
overflow: hidden;
-webkit-animation: spin 10s linear infinite;
        animation: spin 10s linear infinite;
}
.effect-5 div{
   width: 1px;
   background-color: #4dd0e1;
}
/*****effect-5 circular stripes animation  end********/

/*****!effect animation end ********/
